import eventBus from "@/eventBus.js";
let imgs = [];
let timer;

function setImg(img) {
  let { dom, src, isLoad } = img;
  if (isLoad) {
    return; // 已经处理过了。
  }
  const rect = dom.getBoundingClientRect();
  if (
    rect.right > 0 &&
    rect.left < document.documentElement.clientWidth &&
    rect.bottom > 0 &&
    rect.top < document.documentElement.clientHeight
  ) {
    dom.src = src;
    img.isLoad = true;
  }
}
function setImgs() {
  for (let item of imgs) {
    setImg(item);
  }
}
function handleScroll() {
  if (timer) {
    // 防抖，优化
    return;
  }
  timer = setTimeout(() => {
    setImgs();
    timer = null;
  }, 100);
}

eventBus.$on("mainScroll", handleScroll);

export default {
  bind(el, bindings) {
    imgs.push({
      dom: el,
      src: bindings.value,
      isLoad: false,
    });
  },
  unbind(el) {
    // 需要卸载的el
    imgs = imgs.filter((img) => img.dom !== el);
  },
};
